body {
    background-image: linear-gradient(to right, #868f96 0%, #596164 100%);
}

.calculator {
  /* border: 1px solid black; */
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 250px;
  min-height: 300px;
  border-radius: 4px;
  background: #242424;
}
.calculator input {
  width: 100%;
  margin-bottom: 10px;
  font-size: 2.4rem;
  height: 60px;
  color: #fff;
  border: none;
  border-radius: 0;
  background: #242424;
  border-bottom: 1px solid #868686a1;
}

.calculator button {
  border: none;
  height: 100%;
  width: 100%;
  color: rgba(255, 255, 255, 0.623);
  font-size: 2rem;
  font-weight: 100;
  background: #3a3a3a00;
  transition: background 0.3s ease;
}

.calculator button:hover {
  background: #5e5d5dab;
  border-radius: 5px;
}

.xd {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  width: 100%;
}
